.gig-widget {
  display: flex;
  align-items: center;
  justify-content: center;
  position: fixed;
  width: 200px;
  margin-left: -220px;
  /* margin-top: 140px; */
  height: 100%;
  top: 0; }
  .gig-widget .rating .fa {
    color: orange;
    margin: 2px; }
  .gig-widget .mainIcon {
    font-size: 18px;
    width: 24px;
    color: #777; }
  .gig-widget .blog-widget-inner {
    display: flex;
    align-items: flex-start;
    justify-content: flex-end;
    flex-direction: column; }
    .gig-widget .blog-widget-inner .clapped {
      margin-right: 20px; }
      .gig-widget .blog-widget-inner .clapped img {
        width: 44px;
        margin-top: -4px;
        cursor: pointer; }
    .gig-widget .blog-widget-inner .item {
      cursor: pointer;
      margin-bottom: 10px;
      display: flex;
      font-size: 12px;
      align-items: center;
      justify-content: flex-start; }
      .gig-widget .blog-widget-inner .item img {
        width: 20px;
        margin-right: 10px; }
      .gig-widget .blog-widget-inner .item p {
        font-size: 10px;
        color: #666;
        width: 80%;
        padding-left: 35px; }
    .gig-widget .blog-widget-inner .button {
      width: 80%;
      max-width: 100px !important;
      display: flex;
      align-items: center;
      justify-content: space-around; }
  @media only screen and (max-width: 992px) {
    .gig-widget {
      position: relative;
      width: 100%;
      margin: 0 auto; }
      .gig-widget .item {
        margin: 0 10px; }
      .gig-widget .blog-widget-inner {
        align-items: center;
        justify-content: center;
        flex-direction: row; }
      .gig-widget .button {
        margin-bottom: 10px; } }
