.standard-price-container{
  input[type="checkbox"]{
    box-shadow: none !important;
  }
  input[type="checkbox"] + label:before {
    border: 1px solid #6c757d;

  }
  .form-check-input {
    &:checked ~ .form-check-label::before {
      background-color: #000 !important;
    }

    &:checked:focus ~ .form-check-label::before {
      box-shadow: none !important;
    }

    &:focus ~ .form-check-label::before {
      box-shadow: none !important;
    }
    &:active ~ .form-check-label::before {
      background-color: #fff;
    }
  }
}

.agree-form{
  input[type="checkbox"]{
    box-shadow: none !important;
    zoom: 1.3;
    margin-right: 5px;
  }
}

// tooltip message css
.tooltip {
  position: relative;
  display: inline-block;
  opacity: 1
}

.tooltip .tooltiptext {
  visibility: hidden;
  background-color: #fff;
  color: #000;
  text-align: left;
  border-radius: 5px;
  position: fixed;
  right: 5%;
  top: 90%;
  width: 280px;
  transition: opacity 0.3s;
  border: 0.5px solid #f8f9fa;
  padding: 5px;
  box-shadow: 0px 0px 1px 0.5px #0000004d;
}

.tooltip .tooltiptext::after {
  content: "";
}

.tooltip:hover .tooltiptext {
  visibility: visible;
  opacity: 1;
}

